import React from 'react';
import styled from 'styled-components';
import Modal from '@modules/Modal';
import ViewContent from './ViewContent'


const Container = styled.div`
    position: relative;
    padding: 0 20px;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    display: flex;
`;

const Text = styled.span`
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
`;

const RowCell = ({ value, data, modalStateHandler, modalState, ...props }) => {
    return (
        <>
            <Container onClick={() => modalStateHandler(true)}>
                <Text title={value}>{value}</Text>
            </Container>
            <Modal visible={modalState} onRequestClose={() => modalStateHandler(false)}>
               <ViewContent data={data} onRequestClose={() => modalStateHandler(false)} />
            </Modal>
        </>
    );
};

export default RowCell;
