import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import Preloader from '@components/preloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';

const TitleBox = styled.div`
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
`;

export default function Visitors() {
    const [state, setState] = useState({
        fetchedData: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('https://companyapi.sojiare.com/v1/company/visitors', true, true);
            getter.on('static', async (response) => {
                if (response.response) {
                    const data = response.response.data;
                    stateHandler('fetchedData', data);
                }
            });
        })();
    }, []);

    return (
        <div>
            {state.fetchedData ? (
                <>
                    <TitleBox>Visitors</TitleBox>
                    <DataTable getColumns={getColumns} MOCK_DATA={state.fetchedData} texts="No Results fo" />
                </>
            ) : (
                <Preloader />
            )}
        </div>
    );
}
