import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import {CgMenuLeft} from 'react-icons/cg'


const Container = styled.div`
    width: 30px;
    height: 30px;
    border-radius: 35px;
    background: rgba(255, 255, 255, 0.38);
    z-index: 4;
    position: absolute;
    right: 20px;
    top: 25px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export default function Toggle({ handleToggleSidebar, collapsed }) {
    const onClick = () => handleToggleSidebar(!collapsed);
    return (
        <Container onClick={onClick}>
           <CgMenuLeft color={colors.textColor} size={24} style={{fontWeight: 'bold'}} />
        </Container>
    );
}
