import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';

const Container = styled.div`
    background: ${({ error }) => (error ? 'rgba(255, 0, 0, .14)' : '#EFEFEF')};
    border-radius: 10px;
    transition: all 0.3s ease-in-out, background 0s, color 0s, border-color 0s;
    width: 100%;
    min-height: 40px;
    justify-content: center;
    align-items: center;
    display: flex;
    color: ${({ error }) => (error ? '#FF0000' : colors.textColor)};
    padding: 2px;
    margin-bottom: 5px;

    .PhoneInputInput,
    input {
        color: ${({ error }) => (error ? '#FF0000' : colors.textColor)} !important;
        font-weight: 500;
    }
`;

export const Label = styled.p`
    margin-bottom: 10px;
`;

export const Importants = styled.p`
    margin: 2px;
    color: ${colors.dark3};
    display: inline-block;
    font-weight: bold;
`;

export const ErrorText = styled.p`
    margin: 0;
    color: ${colors.red};
    font-size: 11px;
    margin-top: 10px;
`;

export default function Cards({ error, children, nopadding, style }) {
    return (
        <Container error={error} nopadding={nopadding} style={style}>
            {children}
        </Container>
    );
}
