import React, { useEffect, useState } from 'react';
import PhoneInput, { formatPhoneNumber, formatPhoneNumberIntl, isValidPhoneNumber } from 'react-phone-number-input';
import InputCard, { Label, Importants } from '../Card';
import styled from 'styled-components';
import 'react-phone-number-input/style.css';
import { colors } from '@components/typo';
import { IoWarning } from 'react-icons/io5';
import './index.css';

const Container = styled.div`
    position: relative;
    width: 100%;
`;

const PhoneInputContainer = styled.div`
    .PhoneInputCountry {
        padding: 10px;
        background: ${({ error }) => (error ? 'rgba(255, 0, 0, .14)' : '#EFEFEF')};
        margin-right: 10px;
        border-radius: 5px;
    }
    .PhoneInputInput {
        padding: 10px;
        background: ${({ error }) => (error ? 'rgba(255, 0, 0, .14)' : '#EFEFEF')};
        border-radius: 5px;
        color: ${({ error }) => (error ? '#FF0000' : colors.textColor)} !important;
        font-weight: 500;
    }
`;

const ErrorText = styled.div`
    color: ${colors.red};
    font-size: 11px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export default function PhoneInputs({ title, error, errorText, important, value, ...props }) {
    const [phoneError, setError] = useState(false);

    useEffect(() => {
        if (!isNaN(value) && value != '') {
            if (isValidPhoneNumber(value)) {
                setError(false);
            } else {
                setError(true);
            }
        }
    }, [value]);

    return (
        <>
            <Container>
                {title && (
                    <Label>
                        {title} {important && <Importants>*</Importants>}
                    </Label>
                )}
                <PhoneInputContainer error={error || phoneError}>
                    <PhoneInput defaultCountry="NG" {...props} />
                </PhoneInputContainer>
            </Container>
            {errorText && (
                <ErrorText>
                    <IoWarning size={15} /> {errorText}
                </ErrorText>
            )}
        </>
    );
}
