import React, { useState } from 'react';
import styled from 'styled-components';
import Button from '@components/libs/Button';
import { colors } from '@components/typo';
import { Input } from '@components/libs/Forms';

const Container = styled.div`
    .modalContainer {
        width: 500px;
        background-color: #fff;
        border-radius: 10px;
        padding: 15px;
    }
    @media only screen and (max-width: 500px) {
        .modalContainer {
            width: 90vw;
        }
    }
`;

const Header = styled.div`
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
`;

const ActionButtonContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
`;

const TextAreaContainer = styled(Input)``;

const SendButton = styled(Button)`
    width: 150px;
    height: 40px;
    background-color: ${colors.defaultColor};
    color: #fff;
    border-radius: 5px;
`;
const CancelButton = styled(Button)`
    width: 80px;
    height: 40px;
    background-color: ${colors.grey};
    border-radius: 5px;
    color: ${colors.textColor};
`;

const AddNewNumber = styled.div`
    padding: 5px;
    background-color: ${colors.defaultColor};
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    cursor: pointer;
`

const EditPhoneNumber = ({ onRequestClose }) => {
    return (
        <Container>
            <div className="modalContainer">
                <Header>Edit Company Phone Number(s)</Header>

                <TextAreaContainer placeoholder="Phone Number" value="08182828282" />
                <TextAreaContainer placeoholder="Phone Number" value="0908838383838" />
                <TextAreaContainer placeoholder="Phone Number" value="08138383838338 " />
                <AddNewNumber>Add New Number</AddNewNumber>
                <ActionButtonContainer>
                    <CancelButton title="Cancel" onClick={() => onRequestClose()} />
                    <SendButton
                        className="button_container"
                        title="Edit Number"
                        // loading={loading}
                        // onClick={() => addToVerify()}
                    />
                </ActionButtonContainer>
            </div>
        </Container>
    );
};

export default EditPhoneNumber;
