import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';
import Preloader from '@components/preloader';
import { colors } from '@components/typo';
import Modal from '@modules/Modal';
import AddContent from './AddContent';

const ButtonContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;
`;

const Button = styled.div`
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background: ${colors.defaultColor};
    cursor: pointer;
    border-radius: 10px;
    &:hover {
        background-color: rgba(255, 255, 255, 0.2);
    }
`;

export default function VerifiedContent() {
    const [openModal, setOpenModal] = useState(false);
    const [fecthedData, setFetchedData] = useState();

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('https://companyapi.sojiare.com/v1/company/contents', true, true);
            getter.on('static', (response) => {
                if (response.response) {
                    const data = response.response.data;
                    setFetchedData(data);
                }
            });
        })();
    }, []);

    return (
        <>
            <div className="content_Container">
                <ButtonContainer>
                    <Button onClick={() => setOpenModal(true)}>Add Content</Button>
                </ButtonContainer>
                <>
                    {fecthedData ? (
                        <>
                            <DataTable getColumns={getColumns} MOCK_DATA={fecthedData} texts="No Results fo" />
                        </>
                    ) : (
                        <Preloader />
                    )}
                </>
            </div>
            <Modal visible={openModal} onRequestClose={() => setOpenModal(false)}>
                <AddContent onRequestClose={() => setOpenModal(false)} />
            </Modal>
        </>
    );
}
