import React, { lazy, Suspense,useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import {PagePreloader} from '../components/preloader';
import useUserDetails from '@components/Hooks/useUserDetails';
import SojiBaseInit from '@modules/SojiBase';


const HomePage = lazy(() => import('../pages/Users/HomePage'));
const Authe = lazy(() => import('../pages/Authe'));
const CompanyProfile = lazy(() => import('../pages/Users/CompanyProfile'));
const API  = lazy(() => import('../pages/Users/API'));
const HowItWorks = lazy(() => import('../pages/Users/HowItWorks'));
const Pricing = lazy(() => import('../pages/Users/Pricing'));
const AboutUs = lazy(() => import('../pages/Users/AboutUs'));
const Policy = lazy(() => import('../pages/Policy'));
const Terms = lazy(() => import('../pages/Terms'));



// Companies
const CompanyLandingPage = lazy(() => import('../pages/Company/LandingPage'));
const CompanyLogin = lazy(() => import('../pages/Authe/Company/Login'));
const CompanySignUp = lazy(() => import('../pages/Authe/Company/SignUp'));
import AwaitingActivation from '../pages/Company/AwaitingActivation';


export default function GeneralRoutes() {
    const useUserDetail = useUserDetails();
    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/users/user_details');
            getter.on('static', (response) => {
                if (response.response.status != 406) {
                    useUserDetail.set(response.response.data[0]);
                }
            });
        })();
    }, []);
    // 
    return (
        <Suspense fallback={<PagePreloader />}>
            <Routes>
                <Route path="/">
                    <Route index element={<HomePage />} />
                    <Route path="authe/:pageName" element={<Authe />} />
                    <Route path="api" element={<API />} />
                    <Route path="how-it-works" element={<HowItWorks />} />
                    <Route path="pricing" element={<Pricing />} />
                    <Route path="about-us" element={<AboutUs />} />
                    <Route path="policy" element={<Policy />} />
                    <Route path="terms-and-conditions" element={<Terms />} />

         
                    {/* Companies */}
                    <Route path="company/awaiting-activation" element={<AwaitingActivation />} />
                    <Route path="company" element={<CompanyLandingPage />} />
                    <Route path="company/login" element={<CompanyLogin />} />
                    <Route path="company/signup" element={<CompanySignUp />} />
                    <Route path="company/:company_name" element={<CompanyProfile />} />

                </Route>
            </Routes>
        </Suspense>
    );
}