import axios from 'axios';
import Cookies from 'js-cookie';

const SojiBaseStructure = {};

SojiBaseStructure.deleteCookie = () => {
    Cookies.remove('SOJI_uft_token');
};

export const SojiBaseCookie = SojiBaseStructure;

const SojiBaseInstance = async (config) => {
    SojiBaseStructure.config = config;

    SojiBaseStructure.post = async (requestUri, data = {}, otherPostRequest = false) => {
        const apiURL = otherPostRequest ? requestUri : config.apiBaseUrl + requestUri;

        try {
            const request = await axios({
                method: 'post',
                url: apiURL,
                data: data,
                headers: {
                    'content-type': 'application/json',
                    'access-control-allow-origin': '*',
                    ...(otherPostRequest
                        ? { Authorization: config.systemKey + ':' + Cookies.get('SOJI_uft_token') }
                        : { authorization: config.systemKey + ':' + Cookies.get('SOJI_uft_token') }),
                },
            });
            const getResponse = await request.data;
            const response = responseHandler(getResponse);
            return response;
        } catch (error) {
            const { response } = error;
            const { request, ...errorObject } = response;
            const getErrorMessage = responseHandler(errorObject.data);
            return getErrorMessage;
        }
    };

    SojiBaseStructure.get = async (requestUri, otherPostRequest = false) => {
        const apiURL = otherPostRequest ? requestUri : config.apiBaseUrl + requestUri;
        try {
            const request = await axios({
                method: 'get',
                url: apiURL,
                ...(otherPostRequest
                    ? { headers: { Authorization: config.systemKey + ':' + Cookies.get('SOJI_uft_token') } }
                    : { headers: { authorization: config.systemKey + ':' + Cookies.get('SOJI_uft_token') } }),
                Credentials: false,
            });
            const getResponse = await request.data;
            const response = responseHandler(getResponse);
            return response;
        } catch (error) {
            const { response } = error;
            const { request, ...errorObject } = response;
            const getErrorMessage = responseHandler(errorObject.data);
            return getErrorMessage;
        }
    };

    const responseHandler = (data = {}, params = {}) => {
        const returnHandler = {};
        returnHandler.on = (type, callable) => {
            if (type === 'static') {
                return callable(data);
            } else {
                throw new Error(`Unknown fetch dataset, use either (static or watch)`);
            }
        };
        return returnHandler;
    };
    return SojiBaseStructure;
};

export default SojiBaseInstance;
