import React, { useState } from 'react';
import styled from 'styled-components';
import Background from './assets/frame.png';
import Vector from './assets/vector.png';
import Authe from '@components/assets/authe.png';
import './index.css';

import Header from './Header/User';
import UserMenus from './Header/User/Menus';
import Body from './Body';

const Container = styled.div``;

const SideImageContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
`;

const SideImage = styled.div`
    width: 100%;
    height: 100%;
    background-repeat: no-repeat !important;
    position: relative;
    background-size: contain !important;
    background-position: center !important;
    max-height: 500px;
    max-width: 500px;
    position: sticky;
    top: 73px;
`;

const Content = styled.div`
    height: calc(100vh - 72px);
`;

const VectionIllustration = styled.img`
    top: 10%;
    right: -60px;
    width: 124.05px;
    height: 96.4px;
    position: absolute;
    z-index: 10;
`;

const LandingPageStructure = ({
    children,
    description,
    howItworkPage,
    aboutus,
    company,
    companyAuthe,
    fullpage,
    ...props
}) => {
    const [state, setState] = useState({
        openMenu: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const menuHandler = () => {
        stateHandler('openMenu', !state.openMenu);
    };

    return (
        <Container id="mobile_container" className={`mobile_container_wrapper ${state.openMenu && 'navigation'}`}>
            <div id="nav">
                <UserMenus />
            </div>
            <div className="main_container">
                <Header menuHandler={menuHandler} />
                {fullpage ? (
                    children
                ) : (
                    <Content className="row">
                        <div
                            className="hidden-on-mobile col-md-6"
                            style={{ display: 'flex', justifyContent: 'center' }}
                        >
                            {company ? (
                                <SideImageContainer>
                                    <SideImage style={{ background: `url(${company ? Authe : Background})` }}>
                                        <VectionIllustration src={Vector} />
                                    </SideImage>
                                </SideImageContainer>
                            ) : (
                                <SideImage style={{ background: `url(${company ? Authe : Background})` }}>
                                    <VectionIllustration src={Vector} />
                                </SideImage>
                            )}
                        </div>
                        <div className="col-md-6">
                            <Body
                                description={description}
                                company={company}
                                companyAuthe={companyAuthe}
                                howItworkPage={howItworkPage}
                                aboutus={aboutus}
                                {...props}
                            >
                                {children}
                            </Body>
                        </div>
                    </Content>
                )}
            </div>
        </Container>
    );
};

export default LandingPageStructure;
