import React, { useEffect, useState } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import WelcomeWidget from './components/WelcomeWidget';
import RecentDocument from './components/RecentDocuments';
import Preloader from '@components/preloader';

export default function Analysis() {
    const [fecthedData, setFetchedData] = useState();

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.post('https://companyapi.sojiare.com/v1/company/dashboard', {}, true, true);
            getter.on('static', (response) => {
                if (response.response) {
                    const data = response.response.data;
                    setFetchedData(data);
                }
            });
        })();
    }, []);

    return (
        <div>
            {fecthedData ? (
                <>
                    <WelcomeWidget fecthedData={fecthedData} />
                    <RecentDocument fecthedData={fecthedData} />
                </>
            ) : (
                <Preloader />
            )}
        </div>
    );
}

