import React from 'react';
import Modal from '@modules/Modal';
import {colors} from '@components/typo'
import styled from 'styled-components';
import { useRequestLoader } from './useRequestLoader';
import Preloader from '../';

const Container = styled.div`
    width: 100px;
    height: 100px;
    border-radius: 15px;
    background-color: ${colors.backgroundColor};
`;

export default function RequestLoader() {
    const alertState = useRequestLoader();
    const alertObject = { state: false };
    const alert = alertState.get();
    return (
        <Modal visible={alert.state} onRequestClose={() => alertState.set(alertObject)}>
            <Container>
                <Preloader />
            </Container>
        </Modal>
    );
}
