import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import Card from '../Card';

const Container = styled.div`
    position: relative;
    width: 100%;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

const Input = styled.input`
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    height: 40px;
    color: ${colors.textColor};
`;

const TextArea = styled.textarea`
    width: 100%;
    padding: 10px;
    border-radius: 6px;
    min-height: 100px;
`;

export default function TextInputs({ title,style, important, textarea, ...props }) {
    return (
        <Container>
            {title && (
                <Label>
                    {title} {important && <Importants>*</Importants>}
                </Label>
            )}

            <Card>{textarea ? <TextArea {...props} /> : <Input {...props} />}</Card>
        </Container>
    );
}
