import React, { forwardRef } from 'react';
import classNames from 'classnames';

const SidebarHeader = ({ children, className, ...rest }, ref) => {
    const sidebarHeaderRef = ref || React.createRef();
    return (
        <div ref={sidebarHeaderRef} className={classNames('fm-sidebar-header', className)} {...rest}>
            {children}
        </div>
    );
};

export default forwardRef(SidebarHeader);
