import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';

const TitleBox = styled.div`
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
`;

export default function RecentDocument({ fecthedData }) {
    return (
        <div className="content_Container">
            <>
                <TitleBox>Recently Added</TitleBox>
                <DataTable getColumns={getColumns} MOCK_DATA={fecthedData.recentVerified} texts="No Results fo" />
            </>
        </div>
    );
}
