import React, { useState } from 'react';
import FileUploader from './FileUploader';
import styled from 'styled-components';
import MevronBaseInit from '@modules/SojiBase';

const Container = styled.div`
    padding: 10px;
`;

const fileTypes = ['JPG', 'PNG', 'GIF', 'PDF'];

export default function Uploads({ uri, payloadObject=[],onUploadFinished }) {
    const [fileUploadingState, setFileUpoadingState] = useState();

    const handleChange = async (file) => {
        setFileUpoadingState(true);
        const payload = new FormData();
        payloadObject.map((object) => {
            payload.append(object.key, object.value);
        });
        payload.append('file', file);
        const MevronBase = await MevronBaseInit;
        const getter = await MevronBase.post(uri, payload);
        getter.on('static', async (data) => {
            setFileUpoadingState(false);
            const response = await MevronBase.response(data);
            onUploadFinished(response)
        });
    };
// Uploads
    return (
        <Container>
            {fileUploadingState ? (
                <div>Uploading the Document, Please wait...</div>
            ) : (
                <FileUploader handleChange={handleChange} name="file" types={fileTypes} />
            )}
        </Container>
    );
}
