import React, { useState } from 'react';
import Structure from '../../Structure';

import styled from 'styled-components';
import { ImDatabase } from 'react-icons/im';
import { colors } from '@components/typo';

const Container = styled.div`
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    padding: 10px;
`;

const Wrapper = styled.div`
    width: 100%;
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    display: flex;
    text-align: center;
    flex-direction: column;
`;

const Desc = styled.div`
    margin-top: 20px;
`;

export default function AwaitingActivation() {
    return (
        <Structure companyAuthe>
            <Container>
                <Wrapper>
                    <ImDatabase size={38} color={colors.defaultColor} />
                    <Desc>
                        Please wait while the submitted details are being verified. An E-mail will be sent to you once the verification is complete.
                    </Desc>
                </Wrapper>
            </Container>
        </Structure>
    );
}
