import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import styled from 'styled-components';

import Logo from '@components/assets/Logo/NewLogo.svg';

import { FaUserCircle } from 'react-icons/fa';
import Modal from '@modules/Modal';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import useUserDetails from '@components/Hooks/useUserDetails';
import PreviousSearches from '../../../Users/PreviousSearches';
import { BiMenuAltRight } from 'react-icons/bi';
import '../index.css';
import Menus from './Menus';

const MenuContainer = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
`;

const MenuButton = styled.div`
    box-shadow: 0px 2px 3px rgb(0 0 0 / 7%);
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    height: 50px;
    width: 50px;
    cursor: pointer;
`;

const LogoContainer = styled.img`
    height: 40px;
    width: 80px;
`

const UserHeader = ({ menuHandler }) => {
    const isMobile = useMediaQuery({ maxWidth: 767 });
    const useUserDetail = useUserDetails();

    const [state, setState] = useState({
        onPreviousSearchesModal: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const logout = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        useUserDetail.set(false);
    };

    return (
        <>
            <div className="main_header">
                <div className="main_header_wrapper container">
                    <div className="logo_container">
                        <LogoContainer src={Logo} />
                    </div>
                    <div className="nav_bar_container">
                        <Menus />
                    </div>

                    <MenuContainer>
                        {useUserDetail.get() ? (
                            <label className="dropdown">
                                <div className="dd-button">
                                    <div className="action_container">
                                        <FaUserCircle size={30} color={colors.textColor} />
                                        <div className="username">{useUserDetail.get().lastName}</div>
                                    </div>
                                </div>
                                <input type="checkbox" className="dd-input" id="test" />
                                <ul className="dd-menu">
                                    <li onClick={() => stateHandler('onPreviousSearchesModal', true)}>
                                        Previous Searches
                                    </li>
                                    <li>
                                        <Link to="/pricing">Upgrade Account</Link>
                                    </li>
                                    <li className="divider"></li>
                                    <li onClick={() => logout()}>Sign Out</li>
                                </ul>
                            </label>
                        ) : (
                            <div className="action_container">
                                <a href="/authe/login">Login</a>
                                <a href="/authe/signup" style={{ color: '#FF6600' }}>
                                    Sign Up
                                </a>
                            </div>
                        )}

                        {isMobile && (
                            <MenuButton onClick={() => menuHandler()}>
                                <BiMenuAltRight size={30} />
                            </MenuButton>
                        )}
                    </MenuContainer>
                </div>
            </div>
            <Modal
                visible={state.onPreviousSearchesModal}
                onRequestClose={() => stateHandler('onPreviousSearchesModal', false)}
            >
                <PreviousSearches
                    modal
                    onRequestClose={() => stateHandler('onPreviousSearchesModal', false)}
                    state={state}
                />
            </Modal>
        </>
    );
};

export default UserHeader;
