import React, { useState, useEffect } from 'react';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';
import DataTable from '@modules/DataTable';
import getColumns from './Table/Column';
import styled from 'styled-components';
import Preloader from '@components/preloader';

const TitleBox = styled.div`
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
    margin-top: 15px;
`;

export default function VerifiedContent() {
    const [fecthedData, setFetchedData] = useState();

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('https://companyapi.sojiare.com/v1/company/reviews', true, true);
            getter.on('static', (response) => {
                if (response.response) {
                    const data = response.response.data;
                    setFetchedData(data);
                }
            });
        })();
    }, []);

    console.log(fecthedData)

    return (
        <div className="content_Container">
            <>
                {fecthedData ? (
                    <>
                        <DataTable
                            getColumns={getColumns}
                            MOCK_DATA={fecthedData}
                            texts="No Results fo"
                        />
                    </>
                ) : (
                    <Preloader />
                )}
            </>
        </div>
    );
}
