import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import GeneralRoutes from './GeneralRoutes';
import CompanyRoutes from './CompanyRoutes'

const Routers = () => {
    
    const location = useLocation();
    const [isGeneralRoutes, setGeneralRoutes] = useState(true);

    useEffect(() => {
        const arraySet = location.pathname.split('/');
        if (arraySet[2] == 'dashboard') {
            setGeneralRoutes(false);
        } else {
            setGeneralRoutes(true);
        }
    }, [location]);

    return <>{isGeneralRoutes ? <GeneralRoutes /> : <CompanyRoutes />}</>;
};

export default Routers;
