import React, { useState } from 'react';
import AsyncCreatableSelect from 'react-select/async-creatable';
import { v4 as uuidv4 } from 'uuid';
import SojiBaseInit, { config } from '@modules/SojiBase';
import { colors } from '@components/typo';
import Cookies from 'js-cookie';
import axios from 'axios';
import { selectStyles } from '../../selectStyles';

function AutoSuggest(props) {
    const placesID = uuidv4();
    const [inputValue, setValue] = useState('');

    // handle input change event
    const handleInputChange = (value) => {
        setValue(value);
    };

    // load options using API call
    const loadOptions = async (inputValue) => {
        const payload = { search: inputValue };
        const apiURL = config.apiBaseUrl + 'v1/utils/companies/autosuggest';
        const request = await axios({
            method: 'post',
            url: apiURL,
            data: payload,
            headers: {
                'content-type': 'application/json',
                'access-control-allow-origin': '*',
                'authorization': config.systemKey + ':' + Cookies.get('SOJI_uft_token'),
            },
        });
        const getResponse = await request.data;
        if (getResponse.response) {
            if (getResponse.response.data.length > 0) {
                return getResponse.response.data;
            } else {
                return [
                    {
                        companyName: inputValue,
                        pageHandle: inputValue.toLowerCase().replace(/\W/g, ''),
                    },
                ];
            }
        } else {
            return [];
        }
    };

    return (
        <div id={placesID} style={{ width: '100%' }}>
            <AsyncCreatableSelect
                name="colors"
                loadOptions={loadOptions}
                className="basic-multi-select"
                classNamePrefix="select"
                style={{ height: '50px', width: '100%' }}
                menuPortalTarget={document.getElementById(placesID)}
                onMenuOpen={() => {
                    document.getElementById(placesID).setAttribute('style', 'z-index:999999; width: 100%');
                }}
                onMenuClose={() => {
                    document.getElementById(placesID).setAttribute('style', 'z-index:9999;width: 100%');
                }}
                isClearable={true}
                styles={selectStyles}
                cacheOptions
                defaultOptions
                {...props}
            />
        </div>
    );
}

export default AutoSuggest;
