import React from 'react';
import { v4 as uuidv4 } from 'uuid';
import styled from 'styled-components';
import Select from 'react-select';
import { selectStyles } from '../selectStyles';
import './index.css';

const Container = styled.div`
    position: relative;
    margin-bottom: 15px;
    width: 100%;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

const options = [{ value: 'chocolate', label: 'Chocolate' }];

export default function Selects({ children, important, title, ...props }) {
    const placesID = uuidv4();
    return (
        <Container>
            <Label>
                {title} {important && <Importants>*</Importants>}
            </Label>
            <div id={placesID} style={{ width: '100%' }}>
                <Select
                    options={options}
                    name="colors"
                    className="basic-multi-select"
                    classNamePrefix="select"
                    style={{ height: '50px', width: '100%' }}
                    menuPortalTarget={document.getElementById(placesID)}
                    onMenuOpen={() => {
                        document.getElementById(placesID).setAttribute('style', 'z-index:999999; width: 100%');
                    }}
                    onMenuClose={() => {
                        document.getElementById(placesID).setAttribute('style', 'z-index:9999;width: 100%');
                    }}
                    isClearable={true}
                    styles={selectStyles}
                    cacheOptions
                    defaultOptions
                    {...props}
                />
            </div>
        </Container>
    );
}
