import React from 'react';
import FileUploader from '@modules/Upload';
import styled from 'styled-components';
import Card from '../Card';

const Container = styled.div`
    position: relative;
    width: 100%;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

export default function FileUploaders({ title, important, ...props }) {
    return (
        <Container>
            {title && (
                <Label>
                    {title} {important && <Importants>*</Importants>}
                </Label>
            )}

            <Card>
                <FileUploader {...props} />
            </Card>
        </Container>
    );
}
