import React from 'react';
import { Link } from 'react-router-dom';

export default function Menus() {
    return (
        <>
            <Link to="/" className='menu_box'>
                <div className="nav_dot hidden-on-mobile"></div> Home
            </Link>
            <Link to="/company" className='menu_box'>
                <div className="nav_dot hidden-on-mobile"></div> Company
            </Link>
            <Link to="/api">
                <div className="nav_dot hidden-on-mobile"></div>Api
            </Link>
            <Link to="/pricing">
                <div className="nav_dot hidden-on-mobile"></div>Pricing
            </Link>
            <Link to="/how-it-works">
                <div className="nav_dot hidden-on-mobile"></div>How it works
            </Link>
            <Link to="/about-us">
                <div className="nav_dot hidden-on-mobile"></div>About us
            </Link>
        </>
    );
}
