import React, { useState } from 'react';
import styled from 'styled-components';

import './index.css';

const Container = styled.div`
    width: 100%;
`;

const Body = ({ children, description, company, homepage, aboutus, companyAuthe, howItworkPage,userAuthe, API }) => {
    return (
        <Container>
            <div className="container content">
                {homepage ? (
                    children
                ) : (
                    <>
                        {!company && !howItworkPage && !aboutus && !companyAuthe && !API && !userAuthe && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                      We protect you from Phishing/Cybercrime: Verify what is {' '}
                                        <spam className="text_default_color text_bold">Original</spam> or{' '}
                                        <spam className="text_default_color text_bold">Suspicious</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        {howItworkPage && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                        How it <spam className="text_default_color text_bold">Works</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        {aboutus && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                        About <spam className="text_default_color text_bold">Us</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        {companyAuthe && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                        Company <spam className="text_default_color text_bold">Access Control</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        {userAuthe && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                        User <spam className="text_default_color text_bold">Access Control</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        {API && (
                            <div className="content_desc">
                                <div>
                                    <div>
                                        Developer <spam className="text_default_color text_bold">API</spam>
                                    </div>
                                </div>
                            </div>
                        )}

                        <div className="content_wrapper">
                            <div className="content_wrapper_box">
                                <div className="content_body">{children}</div>
                                {description && <div className="box_description">{description}</div>}
                            </div>
                        </div>
                    </>
                )}
            </div>
        </Container>
    );
};

export default Body;
