import React, { useState } from 'react';
import styled from 'styled-components';
import Button from '@components/libs/Button';
import { colors } from '@components/typo';
import {Input} from '@components/libs/Forms'
import SojiBaseInit from '@modules/SojiBase';
import { useAlertState } from '@components/libs/Notify/useAlertState';



const Container = styled.div`
    .modalContainer {
        width: 500px;
        background-color: #fff;
        border-radius: 10px;
        padding: 15px;
    }
    @media only screen and (max-width: 500px) {
        .modalContainer {
            width: 90vw;
        }
    }
`;

const Header = styled.div`
    width: 100%;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
`

const ActionButtonContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
`;

const SendButton = styled(Button)`
    width: 150px;
    height: 40px;
    background-color: ${colors.defaultColor};
    color: #fff;
    border-radius: 5px;
`;
const CancelButton = styled(Button)`
    width: 80px;
    height: 40px;
    background-color: ${colors.grey};
    border-radius: 5px;
    color: ${colors.textColor};
`;

const AddToFlag = ({ onRequestClose }) => {
    const alertState = useAlertState();
    const [content, setContent] = useState()
    const [loading, setLoading] = useState(false)

    const addNewContent = async () => {
        setLoading(true)
        const SojiBase = await SojiBaseInit;
        const payload = {verifiedContent: content}
        const getter = await SojiBase.post('https://companyapi.sojiare.com/v1/company/add_doc', payload,true, true);
        getter.on('static', (response) => {
            setLoading(false)
            onRequestClose()
            if(response.response){
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: response.response.message,
                    type: 'success',
                    onClose: () => window.location.reload()
                });
            }   
        })
    }
   
    return (
        <Container>
            <div className="modalContainer">
                <Header>Add New Content</Header>
                <Input textarea onChange={(e) => setContent(e.target.value)} />
                <ActionButtonContainer>
                    <CancelButton title="Cancel" onClick={() => onRequestClose()} />
                    <SendButton
                        className="button_container"
                        title="Add Content"
                        loading={loading}
                        onClick={() => addNewContent()}
                    />
                </ActionButtonContainer>
            </div>
        </Container>
    );
};

export default AddToFlag;
