import React from 'react';
import styled from 'styled-components';
import Envelop from '@components/assets/envelop.png';
import Scam from '@components/assets/scam.png';
import useUserDetails from '@components/Hooks/useUserDetails';

const MainContainer = styled.div`
    padding: 15px;
`;

const TitleBox = styled.div`
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 15px;
`;

const Container = styled.div`
    padding: 20px;
    background: #f0f7fc;
    border-radius: 23px;
    margin-bottom: 15px;
`;

const BoxContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
`;

const Figure = styled.div`
    font-weight: 600;
    font-size: 25px;
    line-height: 20px;
    color: #ff6600;
`;

const ScamImage = styled.div`
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    height: 100%;
`;

export default function WelcomeWidget({ fecthedData }) {
    const userDetail = useUserDetails().get();

    return (
        <MainContainer>
            <TitleBox>Welcome Back {userDetail.companyName}!</TitleBox>
            <Container>
                <div className="row">
                    <div className="col-md-4">
                        <BoxContainer>
                            <img src={Envelop} />
                            <p>Total Scam Contents</p>
                            <Figure>{fecthedData.totalScam}</Figure>
                        </BoxContainer>
                    </div>
                    <div className="col-md-4">
                        <BoxContainer>
                            <img src={Envelop} />
                            <p>Total Verified Content</p>
                            <Figure>{fecthedData.totalVerified}</Figure>
                        </BoxContainer>
                    </div>
                    <div className="col-md-4">
                        <ScamImage style={{ backgroundImage: `url(${Scam})` }}></ScamImage>
                    </div>
                </div>
            </Container>
        </MainContainer>
    );
}
