/* eslint-disable react/prop-types */
import React, { useEffect, useState } from 'react';
import { AiOutlineFileText } from 'react-icons/ai';
import { MdOutlineSecurity } from 'react-icons/md';
import { useLocation } from 'react-router-dom';
import styled from 'styled-components';
import Toggle from './Toggle';
import { Sidebar, Menu, MenuItem, SidebarHeader, SidebarContent } from './Structure';
import SojiLogo from '@components/assets/Logo/soji_2.png';

const LogoContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 10px;
`;

const Logo = styled.img`
    width: 50px;
    height: 26px;
`;

const SidebarLayout = ({ image, collapsed, rtl, toggled, handleToggleSidebar, handleCollapsedChange }) => {
    const [page, setPage] = useState();
    const location = useLocation();

    useEffect(() => {
        const arraySet = location.pathname.split('/');
        setPage(arraySet[3]);
    }, [location]);

    return (
        <Sidebar
            image={false}
            rtl={rtl}
            collapsed={collapsed}
            toggled={toggled}
            breakPoint="md"
            onToggle={handleToggleSidebar}
        >
            <SidebarHeader>
                {!collapsed && (
                    <LogoContainer>
                        <Logo src={SojiLogo} />
                    </LogoContainer>
                )}
                <Toggle handleToggleSidebar={handleCollapsedChange} collapsed={collapsed} />
            </SidebarHeader>

            <SidebarContent>
                <Menu iconShape="circle">
                    <MenuItem
                        active={page == 'analysis'}
                        to="/company/dashboard/analysis"
                        icon={<AiOutlineFileText color="#82878B" size={20} />}
                    >
                        Analysis
                    </MenuItem>
                    <MenuItem active={page == 'verified_content'} to="/company/dashboard/verified_content" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Verified Contents
                    </MenuItem>
                    <MenuItem active={page == 'reviews'} to="/company/dashboard/reviews" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Reviews
                    </MenuItem>
                    <MenuItem active={page == 'visitors'} to="/company/dashboard/visitors" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Visitors
                    </MenuItem>
                    <MenuItem active={page == 'profile'} to="/company/dashboard/profile" icon={<MdOutlineSecurity color="#82878B" size={20} />}>
                        Profile
                    </MenuItem>
                </Menu>
            </SidebarContent>
        </Sidebar>
    );
};

export default SidebarLayout;
