/* eslint-disable react/jsx-no-target-blank */
import React, { useState } from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import Modal from '@modules/Modal';
import useUserDetails from '@components/Hooks/useUserDetails';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import EditAbout from './EditAbout';
import EditPhoneNumber from './EditPhoneNumber';

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Header = styled.div`
    width: 100%;
    display: flex;
    box-shadow: 1px 1px 4px 0px rgb(0 0 0 / 25%);
    padding: 10px;
    border-radius: 15px;
    line-height: 35px;
`;

const ShoutBioContainer = styled.div``;

const CompanyLogo = styled.div`
    height: 200px;
    width: 200px;
    border-radius: 15px;
    background-color: ${colors.grey};
`;
const Title = styled.div`
    font-size: 18px;
    font-weight: 600;
`;

const Table = styled.div`
    margin-top: 15px;
    box-shadow: 1px 1px 4px 0px rgb(0 0 0 / 25%);
    padding: 10px;
    border-radius: 15px;
    width: 100%;
`;

const TableContainer = styled.div`
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 10px;
`;
const TableTitle = styled.div`
    width: 100%;
    font-weight: 700;
`;
const TableContent = styled.div`
    flex: 1;
`;

const EditButton = styled.span`
    cursor: pointer;
    color: ${colors.defaultColor};
    font-weight: 600;
    margin-right: 15px;
`;

const CopyLinkContainer = styled.div`
    padding: 5px;
    background-color: ${colors.grey};
    cursor: pointer;
    border-radius: 10px;
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
`;

export default function Profile() {
    const [editAboutModal, setEditAboutModal] = useState(false);
    const [editPhoneNumberModal, setEditPhoneNumberModal] = useState(false);
    const userDetail = useUserDetails().get();

    return (
        <>
            <div className="container">
                <Container>
                    <Header className="row">
                        <div className="col-md-3">
                            <CompanyLogo></CompanyLogo>
                        </div>
                        <div className="col-md-9">
                            <ShoutBioContainer>
                                <Title>{userDetail.companyName}</Title>
                                <div>{userDetail.companyCountry}</div>
                                <div>{userDetail.companyMainContact}</div>
                                <div>{userDetail.companyMainEmail}</div>
                                <a href={`https://sojiare.com/company/${userDetail.companyHandle}`} target="_blank">
                                    https://sojiare.com/company/{userDetail.companyHandle}
                                </a>
                                <CopyToClipboard
                                    text={`https://sojiare.com/company/${userDetail.companyHandle}`}
                                    // onCopy={() => setCopied(true)}
                                >
                                    <CopyLinkContainer>Copy to page Handle</CopyLinkContainer>
                                </CopyToClipboard>
                            </ShoutBioContainer>
                        </div>
                    </Header>

                    <Table>
                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Phone Number(s)</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    {JSON.parse(userDetail.companyOtherPhone).map((number) => {
                                        return number + ', ';
                                    })}
                                    <EditButton onClick={() => setEditPhoneNumberModal(true)}>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Email(s)</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    {userDetail.companyOtherEmail} <EditButton>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>Website</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>www.gavel.ng</TableContent>
                            </div>
                        </TableContainer>

                        <TableContainer className="row">
                            <div className="col-md-3">
                                <TableTitle>About</TableTitle>
                            </div>
                            <div className="col-md-9">
                                <TableContent>
                                    {userDetail.companyAbout}
                                    <EditButton onClick={() => setEditAboutModal(true)}>Edit</EditButton>
                                </TableContent>
                            </div>
                        </TableContainer>
                    </Table>
                </Container>
            </div>

            <Modal visible={editAboutModal} onRequestClose={() => setEditAboutModal(false)}>
                <EditAbout onRequestClose={() => setEditAboutModal(false)} />
            </Modal>
            <Modal visible={editPhoneNumberModal} onRequestClose={() => setEditPhoneNumberModal(false)}>
                <EditPhoneNumber onRequestClose={() => setEditPhoneNumberModal(false)} />
            </Modal>
        </>
    );
}
