import React, { lazy, useState, Suspense, useEffect } from 'react';
import { Routes, Route } from 'react-router-dom';
import styled from 'styled-components';
import useUserDetails from '@components/Hooks/useUserDetails';
import SojiBaseInit from '@modules/SojiBase';

import Sidebar from '@components/layouts/Sidebar';
import Header from '@components/layouts/Header';

// Companies
import CompanyAnalysis from '../pages/Company/Dashboard/pages/Analysis';
import VerifiedContent from '../pages/Company/Dashboard/pages/VerifiedContent';
import CompanyReviews from '../pages/Company/Dashboard/pages/Reviews';
import CompanyVisitors from '../pages/Company/Dashboard/pages/Visitors';
import Profile from '../pages/Company/Dashboard/pages/Profile';
import Preloader from '@components/preloader';

const Container = styled.div`
    height: 100vh;
    width: 100vw;
    display: flex;
`;

const Content = styled.div`
    flex: 1;
    overflow-y: auto;
    overflow-x: hidden;
    padding-top: 0px;
    height: 100vh;
    display: flex;
    flex-direction: column;
`;

const ContentWrapper = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 2px;
    padding: 15px;
    background: #fff;
`;

export default function Access() {
    const [collapsed, setCollapsed] = useState(false);
    const [toggled, setToggled] = useState(false);
    const userDetail = useUserDetails();

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('https://companyapi.sojiare.com/v1/company/profile', true, true);
            getter.on('static', (response) => {
                if (response.response) {
                    const data = response.response.data;
                    userDetail.set(data);
                }
            });
        })();
    }, []);

    const handleCollapsedChange = (checked) => {
        console.log(checked);
        setCollapsed(checked);
    };

    const handleToggleSidebar = (value) => {
        setToggled(value);
    };
    return (
        <Container>
            <Sidebar
                collapsed={collapsed}
                toggled={toggled}
                handleToggleSidebar={handleToggleSidebar}
                handleCollapsedChange={handleCollapsedChange}
            />
            <Content>
                <Header />
                <div>
                    <ContentWrapper className="container">
                        <Suspense fallback={<></>}>
                            {userDetail.get() ? (
                                <Routes>
                                    <Route path="/">
                                        <Route path="company/dashboard/analysis" element={<CompanyAnalysis />} />
                                        <Route
                                            path="company/dashboard/verified_content"
                                            element={<VerifiedContent />}
                                        />
                                        <Route path="company/dashboard/reviews" element={<CompanyReviews />} />
                                        <Route path="company/dashboard/visitors" element={<CompanyVisitors />} />
                                        <Route path="company/dashboard/profile" element={<Profile />} />
                                    </Route>
                                </Routes>
                            ) : (
                                <Preloader />
                            )}
                        </Suspense>
                    </ContentWrapper>
                </div>
            </Content>
        </Container>
    );
}
