import React from 'react';
import styled from 'styled-components';
import AutoSuggest from './AutoSuggest';
import Card from '../Card';

const Container = styled.div`
    position: relative;
    width: 100%;
    margin-bottom: 15px;
`;
const Label = styled.div`
    margin-bottom: 10px;
    font-weight: 700;
`;

const Importants = styled.div`
    margin: 2px;
    color: #ff6600;
    display: inline-block;
    font-weight: bold;
`;

export default function AutoSuggests({ title, important, ...props }) {
    return (
        <Container>
            <Label>
                {title} {important && <Importants>*</Importants>}
            </Label>
            <Card>
                <AutoSuggest {...props} />
            </Card>
        </Container>
    );
}
