import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Logo from '@components/assets/Logo/variate_1.png';
import { FaUserCircle } from 'react-icons/fa';
import { colors } from '@components/typo';
import SojiBaseInit from '@modules/SojiBase';
import useUserDetails from '@components/Hooks/useUserDetails';
import styled from 'styled-components';
import { useLocation } from 'react-router-dom';
import { PageTitleList } from './PageTitleList';
import { useNavigate } from 'react-router-dom';


const Container = styled.div`
    min-height: 64px;
    max-height: 64px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 700;
    position: sticky;
    top: 0;
    z-index: 999;
    padding-left: 15px;
    padding-right: 15px;
`;

const Title = styled.div`
    font-size: 18px;
    font-weight: bold;
`;

const Wrapper = styled.div``;

export default function Header() {
    const userDetail = useUserDetails();
    const [page, setPage] = useState();
    const location = useLocation();
    const navigate = useNavigate();


    useEffect(() => {
        const arraySet = location.pathname.split('/');
        setPage(arraySet[3]);
    }, [location]);

    const logout = async () => {
        const SOJIBase = await SojiBaseInit;
        await SOJIBase.deleteCookie();
        userDetail.set(false);
        navigate('/company/login', { replace: true });
    };

    return (
        <Container>
            <Title>{PageTitleList[page]}</Title>
            <Wrapper>
                <label className="dropdown">
                    <div className="dd-button">
                        <div className="action_container">
                            <FaUserCircle size={30} color={colors.textColor} />
                            <div className="username">{userDetail.get().companyName}</div>
                        </div>
                    </div>
                    <input type="checkbox" className="dd-input" id="test" />
                    <ul className="dd-menu">
                        <li>
                            <Link to="company/dashboard/profile">Profile</Link>
                        </li>
                        <li className="divider"></li>
                        <li onClick={() => logout()}>Sign Out</li>
                    </ul>
                </label>
            </Wrapper>
        </Container>
    );
}
